<template>
    <el-main>
        <el-row :gutter="20">
            <el-col :span="24">
                <div class="box">
                    <div class="info-header">
                        磁盘
                    </div>
                    <div class="info-body clearfix">
                        <div class="border-right col-3">
                            <span class="info-body-header">总容量</span>
                            <br>
                            <span class="text-primary">{{diskSize}}</span>
                            <span class="mark">GB</span>
                        </div>
                        <div class="border-right col-3">
                            <span class="info-body-header">已使用</span>
                            <br>
                            <span class="text-success">{{diskUsed}}</span>
                            <span class="mark">GB</span>
                        </div>
                        <div class="col-3">
                            <span class="info-body-header">剩余</span>
                            <br>
                            <span class="text-success">{{diskFree}}</span>
                            <span class="mark">GB</span>
                        </div>
                    </div>
                </div>
            </el-col>
        </el-row>
        <el-row class="info-row" :gutter="20">
            <el-col :span="24">
                <div class="box">
                    <div class="info-header">
                        文件数
                    </div>
                    <div class="info-body clearfix">
                        <div class="border-right col-2">
                            <span class="info-body-header">总文件数</span>
                            <br>
                            <span class="text-success">{{fileNum}}</span>
                        </div>
                        <div class="col-2">
                            <span class="info-body-header">无效文件数</span>
                            <br>
                            <el-tooltip effect="light" placement="right">
                                <div slot="content">每天凌晨2点<br/>清理废弃文件</div>
                                <span class="text-warn">{{illegalFileNum}}</span>
                            </el-tooltip>
                        </div>
                    </div>
                </div>
            </el-col>
        </el-row>
        <el-row class="info-row" :gutter="20">
            <el-col :span="8">
                <div class="box">
                    <div class="info-header">
                        文件夹数
                    </div>
                    <div class="info-body clearfix">
                        <span class="text-success">{{folderNum}}</span>
                    </div>
                </div>
            </el-col>
            <el-col :span="8">
                <div class="box">
                    <div class="info-header">
                        视频数
                    </div>
                    <div class="info-body clearfix">
                        <span class="text-success">{{videoNum}}</span>
                    </div>
                </div>
            </el-col>
            <el-col :span="8">
                <div class="box">
                    <div class="info-header">
                        音频数
                    </div>
                    <div class="info-body clearfix">
                        <span class="text-success">{{audioNum}}</span>
                    </div>
                </div>
            </el-col>
        </el-row>
    </el-main>
</template>

<script>
import {sysInfo} from '@/api/resource.js'

export default {
    data () {
        return {
            diskSize: 0,
            diskUsed: 0,
            diskFree: 0,
            fileNum: 0,
            illegalFileNum: 0,
            folderNum: 0,
            videoNum: 0,
            audioNum: 0
        }
    },
    created () {
        sysInfo().then((response) => {
            var data = response.data
            this.diskSize = (data.diskCapacity / (1024 * 1024 * 1024)).toFixed(2)
            this.diskUsed = ((data.diskCapacity - data.diskFree) / (1024 * 1024 * 1024)).toFixed(2)
            this.diskFree = (data.diskFree / (1024 * 1024 * 1024)).toFixed(2)
            this.fileNum = data.fileNum
            this.folderNum = data.folderNum
            this.illegalFileNum = data.garbageFileNum
            this.videoNum = data.videoNum
            this.audioNum = data.audioNum
        })
    }
}
</script>

